				"bodyHTML": "<p>Great blog post that explores the advantages and disadvantages of different social media structures.</p>\n<p>He explores when you want to follow ideas (when the community is small) or people (when the community is too big to follow everything).</p>\n<p>He discusses the search/browse debate and concludes you need both. Different people tend toward one style or the other but both are needed for different purposes. Search is fast and takes you straight to content you're interested in. Browse, on other hand, tends to be slower but helps you understand the context and organization of the content better.</p>\n<p>Great stuff. Read more here:</p>\n<p><a href=\"http://smoothspan.wordpress.com/2008/12/12/blackwhite-symmetryasymmetry-forumsocial-network-searchbrowse/\">http://smoothspan.wordpress.com/2008/12/12/blackwhite-symmetryasymmetry-forumsocial-network-searchbrowse/</a></p>\n",
